<template lang="html">
<div id="appNotas" class="container-fluid bg-dark">
    <div class="d-flex flex-column container-fluid flex-grow" id="contenedor">
            <div class="row text-white">
                <div class="col-md-10 offset-md-1">
                    <h1 class="text-white m-3 text-center">Lista de Tareas</h1>
                </div>
                <div class="col-12 col-md-8 div-add">
                    <input type="text" v-on:keydown.enter="add" class="form-control-lg form-control add" :value="texto" @input="evt=>texto = evt.target.value" name="" id="texto" aria-describedby="helpId" placeholder="¿Qué quieres recordar?" required>
                </div>
                <div class="col-12 col-md-4">
                    <div class="row justify-content-between">
                      <div class="col-5 col-md-6 divadd">
                        <button class="btn w-100 add" :class='texto == "" ? "disabled btn-secondary" : "btn-danger"' v-on:click="add">Añadir</button>
                      </div>
                      <div class="col-5 offset-1 col-md-6 offset-md-0 divadd">
                        <label for="upload" class="btn w-100 add" :class='texto == "" ? "disabled btn-success" : "btn-success"'>Adjuntar archivo</label>
                      </div>
                    </div>
                </div>
                <hr class="col-12 col-md-10 offset-md-1 bg-light p-0">
                <div class="col-12 col-md-8 offset-md-1">
                    <span>{{nCompletadas}} tareas completadas de un total de {{nTareas}}</span>
                </div>
                <div class="col-12 col-md-8 offset-md-1">
                    <span class="h6 text-warning" id="eliminar" v-on:click="eliminaCompletados"><b>x</b> Eliminar completados</span>
                </div>
                <div class="dropdown pl-0 col-12 col-md-2 offset-md-1" id="dropdown">
                  <button class="btn btn-secondary dropdown-toggle" type="button" id="triggerId" data-toggle="dropdown" aria-haspopup="true"
                      aria-expanded="false">
                        Ordenar:
                      </button>
                  <div class="dropdown-menu col-12 col-md-4 offset-md-1" aria-labelledby="triggerId">
                    <button class="dropdown-item" @click="ordenar(1)" id="ascendente" name="orden" value="1">Ascendente</button>
                    <button class="dropdown-item" @click="ordenar(2)" id="descendente" name="orden" value="2">Descendente</button>
                    <button class="dropdown-item" @click="ordenar(3)" id="prioridad" name="orden" value="3">Prioridad</button>
                  </div>
                </div>
                <hr class="col-12 col-md-10 offset-md-1 bg-light p-0">
                <div class="col-12 col-md-10 offset-md-1" id="tareas">
                    <ul class="list-group list-group-flush">
                        <div class ="col-12 col-md-2 mb-2">
                            <input type="text" class="form-control" :value="textoBusqueda" @input="evt=>textoBusqueda = evt.target.value" aria-describedby="helpId" placeholder="filtrar">
                        </div>
                        <nota v-for="(todo,index) in listaOrdenada" v-bind:todo="todo" :index="index" v-bind:key="index" @prioridadCambiada="changePriority(todo.id,todo.priority)" @check="changeCheck(todo)" v-on:click="borrar(todo.id)">
                        </nota>
                    </ul>
                </div>
            </div>
      </div>
      <input type="file" name="upload" @change="addArchivo" style="visibility:hidden;" id="upload">
  </div>
</template>

<script lang="js">
/* eslint-disable */
  import { db } from '../db'
  import nota from './nota.vue'
  import firebase from 'firebase'

var moment = require('moment');

  export default  {
    name: 'app-notas',
    components: {
      nota,
    },
    props: [],
    mounted () {

    },
    data () {
      return{
        todos: [
        ],
        nuevaNota: {
        },
        archivoSubido: null,
        listavisible: true,
        texto:"",
        orden: 1,
        textoBusqueda: ""
      }
    },
    firestore: {
      todos: db.collection('notas')
    },
    methods:{
      addArchivo(e){
        this.archivoSubido = e.target.files[0];
      },
      add: function(){
          if(this.texto != ""){
            let archivoSubir = {};
            if(this.archivoSubido){
              firebase.storage().ref(`${this.archivoSubido.name}`).put(this.archivoSubido);
              firebase.storage().ref().child(this.archivoSubido.name).getDownloadURL().then(
                url =>{
                  archivoSubir.url = url;
                  archivoSubir.nombreArchivo = this.archivoSubido.name;
                  archivoSubir.text = this.texto;
                  this.texto = "";
                  archivoSubir.date = moment().format('MMMM Do YYYY, h:mm:ss a');
                  archivoSubir.priority = 1;
                  archivoSubir.checked = false;
                  db.collection('notas').add(archivoSubir);
                  //db.collection('notas').add(this.nuevaNota);
                }
              )
            }
            else{
              //this.todos.push({text: this.texto, checked:false, date: new Date(), symbol:"○",priority:1, id: this.generarUID});
              archivoSubir.text = this.texto;
              this.texto = "";
              archivoSubir.date = moment().format('MMMM Do YYYY, h:mm:ss a');
              archivoSubir.priority = 1;
              archivoSubir.checked = false;
              db.collection('notas').add(archivoSubir);
            }
          }
      },
      borrar: function(id){
        db.collection('notas').doc(id).delete()
      },
      eliminaCompletados: function(){
          this.todos = this.todos.filter((todo)=>{
              return !todo.checked;
          })
      },
      changePriority: function(id,prioridad){
        db.collection('notas').doc(id).update({
          priority : prioridad
        })
      },
      changeCheck: function(todo){
        db.collection('notas').doc(todo.id).update({
          checked : todo.checked
        })
      },
      ordenar: function(nOrden){
        this.orden = nOrden;
      },
    },
computed:{
        nCompletadas: function(){
            let tareas = 0;
            this.todos.forEach((todo)=>{
                if(todo.checked)
                    tareas++;
            })
            return tareas;
        },
        nTareas: function(){
            return this.todos.length;
        },
        listaOrdenada: function(){
            let lista = this.todos.slice();

            if(this.textoBusqueda.length > 0){
                lista = this.todos.slice().filter((todo)=>{
                    return todo.text.includes(this.textoBusqueda);
                })
            }
            if(this.orden == 1){
                return lista.sort((nota1,nota2)=> {
                    var a1 = nota1.text.toLowerCase();
                    var b1 = nota2.text.toLowerCase();
                    return a1 < b1 ? -1 : a1 > b1 ? 1 : 0;
                });
            }
            else if(this.orden == 3){
            return lista.sort((nota1,nota2)=> {
                    var a1 = nota1.priority;
                    var b1 = nota2.priority;
                    return a1 > b1 ? -1 : a1 < b1 ? 1 : 0;
                });
            }
            else{
                return lista.sort((nota1,nota2)=> {
                    var a1 = nota1.text.toLowerCase();
                    var b1 = nota2.text.toLowerCase();
                    return a1 > b1 ? -1 : a1 < b1 ? 1 : 0;
                });
            }
        },
    },
}


</script>

<style>
.boton{
  height:20px;
}
.list-group{
    border-color:white;
}
body{
    min-height:100vh;
}

#eliminar{
    cursor:pointer;
}
ul{
    padding-left:0;
}
