<template>
  <div id="app" class="container-fluid bg-dark">
    <nav class="navbar navbar-expand navbar-dark bg-dark justify-content-between">
        <a class="navbar-brand" href="#">Lista de tareas</a>
        <span class="text-white mr-3" id="nombreUsuario"></span>
        <ul class="nav navbar-nav">
            <button class="btn btn-danger" style="display:none" id="logout" @click="logout">Logout</button>
        </ul>
    </nav>
    <router-view></router-view>
  </div>
</template>

<script>
/* eslint-disable */
import { db } from './db'
import nota from './components/nota.vue'
import pie from './components/pie.vue'
import firebase from 'firebase'
import router from './main.js';
var moment = require('moment');

const $ = require('jquery')
window.$ = $

export default {
  name: 'app',
  components: {
    nota,
  },
  created(){
    firebase.auth().onAuthStateChanged(user => {
        if (user) {
            this.user = user;
            this.$router.replace("/notas");
            $("#nombreUsuario").html(firebase.auth().currentUser.email);
            $("#logout").css("display","");
        }
        else {
            $("#nombreUsuario").html("");
            $("#logout").css("display","none");
        }
    });
  },
  data(){
      return{
        todos: [
        ],
        nuevaNota: {
        },
        listavisible: true,
        texto:"",
        orden: 1,
        textoBusqueda: ""
      }
  },
  firestore: {
    todos: db.collection('notas')
  },
    computed:{
        nCompletadas: function(){
            let tareas = 0;
            this.todos.forEach((todo)=>{
                if(todo.checked)
                    tareas++;
            })
            return tareas;
        },
        nTareas: function(){
            return this.todos.length;
        },
        listaOrdenada: function(){
            let lista = this.todos.slice();

            if(this.textoBusqueda.length > 0){
                lista = this.todos.slice().filter((todo)=>{
                    return todo.text.includes(this.textoBusqueda);
                })
            }
            if(this.orden == 1){
                return lista.sort((nota1,nota2)=> {
                    var a1 = nota1.text.toLowerCase();
                    var b1 = nota2.text.toLowerCase();
                    return a1 < b1 ? -1 : a1 > b1 ? 1 : 0;
                });
            }
            else if(this.orden == 3){
            return lista.sort((nota1,nota2)=> {
                    var a1 = nota1.priority;
                    var b1 = nota2.priority;
                    return a1 > b1 ? -1 : a1 < b1 ? 1 : 0;
                });
            }
            else{
                return lista.sort((nota1,nota2)=> {
                    var a1 = nota1.text.toLowerCase();
                    var b1 = nota2.text.toLowerCase();
                    return a1 > b1 ? -1 : a1 < b1 ? 1 : 0;
                });
            }
        },
    },
    methods:{
      add: function(){
          if(this.texto != ""){
            //this.todos.push({text: this.texto, checked:false, date: new Date(), symbol:"○",priority:1, id: this.generarUID});
            this.nuevaNota.text = this.texto;
            this.texto = "";
            this.nuevaNota.date = moment().format('MMMM Do YYYY, h:mm:ss a');
            this.nuevaNota.priority = 1;
            this.nuevaNota.checked = false;
            db.collection('notas').add(this.nuevaNota);
            }
      },
      borrar: function(nota){
        this.todos = this.todos.filter((todo)=>{
              return todo != nota;
          })
      },
      eliminaCompletados: function(){
          this.todos = this.todos.filter((todo)=>{
              return !todo.checked;
          })
      },
      changePriority: function(){
        this.$forceUpdate()
      },
      ordenar: function(nOrden){
        this.orden = nOrden;
      },
      logout: function(){
        firebase.auth().signOut().then(()=>{
            this.$router.replace('login');
        })
      },
    }
  }
/* eslint-enable */
</script>

